import React, {useEffect, useState} from 'react'
import ReactHLS from 'react-hls';
import {Card} from 'react-bootstrap'
import noImg from '../no-img.png'
import {Link} from 'react-router-dom'
import RenderChannels from './RenderChannels'
function RenderLive({match}) {
    useEffect(
        () => {
            fetchChannel();
            console.log(match.params.id);
        },[]
    )
    const [channel, setChannel] = useState([])
    const fetchChannel = async () => {
        const data = await fetch('https://iptv-org.github.io/iptv/channels.json');
        const channelJson = await data.json();
        const selectedChannel = match.params.id
        setChannel(channelJson[selectedChannel])
        console.log(channel)
    }
    return(
        <>
            <div className="container">
                <div className="flex flex-col w-100">
                    <ReactHLS url={channel.url} autoplay />
                    <Card  className="chcard col-lg-3 w-100 my-3 mx-1 d-flex flex-row ">
                        {channel.logo ? <img src={channel.logo} alt={channel.name}/> : <img src={noImg} alt={channel.name}/>}
                        <h1 className="align-self-center m-0 ml-2">{channel.name}</h1>
                    </Card>
                </div>
            </div>
        </>
    )
}

export default RenderLive;