import React, {useState, useEffect} from 'react';
import noImg from '../no-img.png'
import {Link} from 'react-router-dom'
function RenderChannels() {
useEffect(
    () => {
        fetchChannels();
    },[]
)
const [channels, setChannels] = useState([]);
const [channelsOnview, setChannelsonview] = useState(100);
const fetchChannels = async () => {
    const data = await fetch('https://iptv-org.github.io/iptv/channels.json');
    const channelsJson = await data.json();
    setChannels(channelsJson)
}
  return (
    <div className="container chcontainer">
        {channels.map((channel, index) => {
            if(index >= channelsOnview){
                return null
            }
            return(
                <>
                    <Link to={`/${index}`} className="chcard mx-2 my-3 d-flex flex-row ">
                        {!channel.logo || channel.logo == null ? <img src={noImg} alt={channel.name}/> : <img src={channel.logo} alt={channel.name}/>}
                            <h1 className="align-self-center">{channel.name}</h1>
                            
                    </Link>
                </>
            )
        })}
        <button className="btn btn-warning" onClick={() => { setChannelsonview(channelsOnview + 100) }}>More...</button>
    </div>
  );
}

export default RenderChannels;